//////////////////// filled kyc deatils and ank details :



import React, { useState, useEffect } from "react";
import axios from "axios";

const KYC = () => {
  const [kycDetails, setKycDetails] = useState({
    customer_name: "",
    aadhar_card_number: "",
    pan_number: "",
  });

  const [bankDetails, setBankDetails] = useState({
    account_holder_name: "",
    bank_account_number: "",
    ifsc_code: "",
    branch_name: "",
  });

  const [allKycData, setAllKycData] = useState([]);

  // Handle Input Change
  const handleKycChange = (e) => {
    setKycDetails({ ...kycDetails, [e.target.name]: e.target.value });
  };

  const handleBankChange = (e) => {
    setBankDetails({ ...bankDetails, [e.target.name]: e.target.value });
  };

  // Submit KYC Details
  const submitKyc = async (e) => {
    e.preventDefault();
    try {
      const kycResponse = await axios.post("/api/kyc/", kycDetails);
      const kycId = kycResponse.data.id;
      await axios.post("/api/bankdetails/", { ...bankDetails, kyc: kycId });
      alert("KYC and Bank details submitted successfully!");
    } catch (error) {
      console.error("Error submitting KYC:", error);
    }
  };

  // Fetch All KYC Details
  const fetchKycDetails = async () => {
    try {
      const response = await axios.get("/api/kyc/");
      setAllKycData(response.data);
    } catch (error) {
      console.error("Error fetching KYC details:", error);
    }
  };

  useEffect(() => {
    fetchKycDetails();
  }, []);

  return (
    <div>
      <h1>KYC Form</h1>
      <form onSubmit={submitKyc}>
        <div>
          <label>Customer Name:</label>
          <input
            type="text"
            name="customer_name"
            value={kycDetails.customer_name}
            onChange={handleKycChange}
            required
          />
        </div>
        <div>
          <label>Aadhar Card Number:</label>
          <input
            type="text"
            name="aadhar_card_number"
            value={kycDetails.aadhar_card_number}
            onChange={handleKycChange}
            required
          />
        </div>
        <div>
          <label>PAN Number:</label>
          <input
            type="text"
            name="pan_number"
            value={kycDetails.pan_number}
            onChange={handleKycChange}
            required
          />
        </div>
        <h2>Bank Details</h2>
        <div>
          <label>Account Holder Name:</label>
          <input
            type="text"
            name="account_holder_name"
            value={bankDetails.account_holder_name}
            onChange={handleBankChange}
            required
          />
        </div>
        <div>
          <label>Bank Account Number:</label>
          <input
            type="text"
            name="bank_account_number"
            value={bankDetails.bank_account_number}
            onChange={handleBankChange}
            required
          />
        </div>
        <div>
          <label>IFSC Code:</label>
          <input
            type="text"
            name="ifsc_code"
            value={bankDetails.ifsc_code}
            onChange={handleBankChange}
            required
          />
        </div>
        <div>
          <label>Branch Name:</label>
          <input
            type="text"
            name="branch_name"
            value={bankDetails.branch_name}
            onChange={handleBankChange}
            required
          />
        </div>
        <button type="submit">Submit</button>
      </form>

      <h2>All KYC Data</h2>
      <ul>
        {allKycData.map((kyc) => (
          <li key={kyc.id}>
            {kyc.customer_name} - Aadhar: {kyc.aadhar_card_number} - PAN:{" "}
            {kyc.pan_number}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default KYC;




