////// new contactfrom frontend



import React, { useState, useEffect } from 'react';


const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    emailname: '',
    phonenumber: '',
    message: '',
  });

  const [formStatus, setFormStatus] = useState('');
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  
  // State for form validation
  const [errors, setErrors] = useState({
    name: '',
    emailname: '',
    phonenumber: '',
    message: '',
  });

  // Simulate page loading
  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
    
    // Clear error message when user types
    setErrors({
      ...errors,
      [e.target.name]: '',
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true); // Start loader when submitting

    // Simple validation checks
    let newErrors = {};
    if (!formData.name) newErrors.name = 'Name is required.';
    if (!formData.emailname) newErrors.emailname = 'Email is required.';
    if (!formData.phonenumber) newErrors.phonenumber = 'Phone is required.';
    if (!formData.message) newErrors.message = 'Message is required.';
    
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      setSubmitting(false);
      return; // Prevent submission if there are errors
    }

    try {
      const response = await fetch('http://localhost:8000/contactform/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        setFormStatus('Message sent successfully!');
        setIsSubmitted(true);
      } else {
        setFormStatus('Failed to send message.');
      }
    } catch (error) {
      console.error('Error:', error);
      setFormStatus('Error submitting the form.');
    }

    setSubmitting(false); // Stop loader after submission
  };



  return (
    <div>
  

      {/* Contact Start */}
      <div className="container-fluid contact py-5">
        <div className="container py-5">
          <div className="p-5 bg-light rounded">
            <div className="row g-4">
              <div className="col-12">
                <div className="text-center mx-auto" style={{ maxWidth: "700px" }}>
                  <h1 className="text-primary">Get in touch</h1>
                  <p className="mb-4">Fill out the form below and we'll get back to you as soon as possible.</p>
                </div>
              </div>
              <div className="col-lg-7">
                {formStatus && (
                  <p style={{ textAlign: 'center', color: isSubmitted ? 'green' : 'red' }}>{formStatus}</p>
                )}
                {isSubmitted ? (
                  <p style={{ textAlign: 'center' }}>Thank you! Our team will contact you soon.</p>
                ) : (
                  <form className="" onSubmit={handleSubmit}>
                    <input
                      name="name"
                      className={`w-100 form-control border-0 py-3 mb-4 ${errors.name ? 'error' : ''}`}
                      placeholder="Your Name"
                      type="text"
                      value={formData.name}
                      onChange={handleChange}
                    />
                    {errors.name && <p className="error-message">{errors.name}</p>}
                    
                    <input
                      name="emailname"
                      className={`w-100 form-control border-0 py-3 mb-4 ${errors.emailname ? 'error' : ''}`}
                      placeholder="Enter Your Email"
                      type="email"
                      value={formData.emailname}
                      onChange={handleChange}
                    />
                    {errors.emailname && <p className="error-message">{errors.emailname}</p>}
                    
                    <input
                      name="phonenumber"
                      className={`w-100 form-control border-0 py-3 mb-4 ${errors.phonenumber ? 'error' : ''}`}
                      placeholder="Phone Number"
                      type="text"
                      value={formData.phonenumber}
                      onChange={handleChange}
                    />
                    {errors.phonenumber && <p className="error-message">{errors.phonenumber}</p>}

                    <textarea
                      name="message"
                      className={`w-100 form-control border-0 mb-4 ${errors.message ? 'error' : ''}`}
                      rows="5"
                      placeholder="Your Message"
                      value={formData.message}
                      onChange={handleChange}
                    ></textarea>
                    {errors.message && <p className="error-message">{errors.message}</p>}

                    <button className="w-100 btn form-control border-secondary py-3 bg-white text-primary" type="submit" disabled={submitting}>
                    </button>
                  </form>
                )}
              </div>
              <div className="col-lg-5">
                <div className="d-flex p-4 rounded mb-4 bg-white">
                  <i className="fas fa-map-marker-alt fa-2x text-primary me-4"></i>
                  <div>
                    <h4>Address</h4>
                    <p className="mb-2">No-14/3, Graffic Agencies, Kolikodu, Kerala - 673001</p>
                  </div>
                </div>
                <div className="d-flex p-4 rounded mb-4 bg-white">
                  <i className="fas fa-envelope fa-2x text-primary me-4"></i>
                  <div>
                    <h4>Mail Us</h4>
                    <p className="mb-2">sales@product.in</p>
                  </div>
                </div>
                <div className="d-flex p-4 rounded bg-white">
                  <i className="fa fa-phone-alt fa-2x text-primary me-4"></i>
                  <div>
                    <h4>Telephone</h4>
                    <p className="mb-2">+91 9876543210</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      
    </div>
  );
};

export default Contact;




