import React from "react";
import "./Notfound.css";

function Notfound() {
  return (
    <div>
      <div class="content-center">
        <h2 class="error">404</h2>
        <p>PAGE NOT FOUND</p>
        <p>It looks like nothing was found at this location.</p>
        <a href="/">
        <button>BACK TO HOME</button>
        </a>
      </div>
    </div>
  );
}

export default Notfound;
