// export default Navmenu;


//////// if user login the website the show the logout button only
////// if user without login website show the dropdown (login/signup)

import React from "react";
import "./Navmenu.css";
import { useNavigate, Link } from "react-router-dom";

function Navmenu() {
  const navigate = useNavigate();

  // Check if the user is logged in
  const isLoggedIn = !!localStorage.getItem("token");

  const handleLogout = () => {
    localStorage.removeItem("token");
    navigate("/login");
  };

  return (
    <div>
      <nav className="navbar navbar-expand-lg bg-white bg-gradient p-3">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            ProPerties
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/invest" >
                  Invest
                </Link>
              </li>
            </ul>
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/about" >
                  About
                </Link>
              </li>
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/contact">
                  Contact
                </Link>
              </li>
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/razorpay">
                  Payment
                </Link>
              </li>
              <li className="nav-item mx-1">
                <Link className="nav-link active" aria-current="page" to="/kyc">
                  KYC - Bank details
                </Link>
              </li>

              {/* Conditionally render based on login status */}
              {isLoggedIn ? (
                <li className="nav-item mx-1" onClick={handleLogout}>
                  <Link className="nav-link active" aria-current="page" style={{cursor:"pointer"}}>
                    Logout
                  </Link>
                </li>
              ) : (
                <li className="nav-item dropdown mx-1">
                  <Link className="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown"  aria-expanded="false" >
                    Dropdown
                  </Link>
                  <ul className="dropdown-menu">
                    <li>
                      <Link className="dropdown-item" to="/login">
                        Login
                      </Link>
                    </li>
                    <li>
                      <Link className="dropdown-item" to="/signup">
                        Signup
                      </Link>
                    </li>
                  </ul>
                </li>
              )}
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navmenu;
